.aboutMeContainer > .aboutMeContainer__titulo {
  position: relative;
  text-align: center;
  font-size: 1.75rem;
  font-weight: 600;
  color: var(--color-titulo);
  margin-top: 2rem;
  margin-bottom: 2.5rem;
  padding-left: 0.35rem;
  padding-bottom: 1rem;
  text-align: center;
}
.aboutMeContainer > .aboutMeContainer__titulo::after {
  content: "¿Quien soy?";
  position: absolute;
  top: 1.75rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.25rem;
  color: var(--color-principal);
  padding: 0.5rem;
  background: var(--color-blanco);
  text-align: center;
}
.aboutMeContainer > .aboutMeContainer__titulo::before {
  content: "";
  position: absolute;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  width: 12rem;
  height: 0.2rem;
  background-color: var(--color-titulo);
}

.technologyContainer > .technologyContainer__navLinks {
  /* background-color: orange; */
  display: flex;
  justify-content: space-evenly;
  margin-top: 1rem;
}

.technologyContainer > .technologyContainer__navLinks > a {
  color: #000;
  font-size: 0.875rem;
  font-weight: 600;
  padding: 0.25rem;
  transition: all 1s ease;
  opacity: 1;
}

.technologyContainer__navLinksActive,
.technologyContainer > .technologyContainer__navLinks > a:hover {
  color: var(--color-blanco) !important;
  background-color: var(--color-principal);
  font-weight: 600;
  opacity: 0.75;
}


/* Para tablets*/
@media screen and (min-width: 768px) {
  .aboutMeContainer > .aboutMeContainer__titulo{
    margin-top: 4rem;
  }
  .technologyContainer {
    width: 70%;
    margin: 0 auto;
  }
}

/* Escritorio  */
@media screen and (min-width: 1024px) {
  .aboutMeContainer {
    display: grid;
    grid-template-columns: repeat(3, 32%);
    justify-content: space-between;
    align-content: center;
  }

  .aboutMeContainer > .aboutMeContainer__titulo {
    grid-column: span 3;
    margin-top: var(--header-nav-height);
  }

  .article-space {
    margin-bottom: 0;
  }

  .aboutMeContainer__skills > .technologyContainer {
    width: 100%;
  }
}