.portfolioModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;

  width: 100%;
  height: 100vh;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.4);
   /* efecto borroso de fondo */
  backdrop-filter: blur(0.75rem);

  display: flex;
  justify-content: center;
  align-items: center;

  opacity: 0;
  pointer-events: none;
  transform: scale(0);

  transition: opacity 0.7s ease-in-out, pointer-events 0.7s ease-in-out,
    transform 0.7s ease-in-out;
}

.openPortafolioModal {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1);
}